class TransfersTableComponent {
    constructor(transferState, bottomSheet) {
        this.transferState = transferState;
        this.bottomSheet = bottomSheet;
        this.onDestroy = new Subject();
        this.transfersTableColumn = ['date', 'from', 'to', 'user', 'amount', 'note', 'action'];
        this.transfersDatasource = new MatTableDataSource([]);
        transferState.transfers.pipe(takeUntil(this.onDestroy)).subscribe(value => {
            this.transfersDatasource.data = value;
        });
    }
    ngOnInit() {
        this.transferState.fetch(10, 0);
        this.transferState.countAll();
    }
    ngOnDestroy() {
        this.onDestroy.next();
    }
    loadPage($event) {
        const skip = (($event.pageIndex + 1) * $event.pageSize);
        const size = $event.pageSize;
        this.transferState.fetch(size, skip);
    }
    viewItems(element) {
        this.bottomSheet.open(TransfersItemsViewComponent, {
            data: {
                transfer: element
            }
        });
    }
    printTransfer(element) {
        this.bottomSheet.open(TransfersExportOptionsComponent, {
            data: {
                transfer: element
            }
        });
    }
}
